.iframe1 {
    position:relative;
    display:block;
    min-height: 400px;
    width: 800px;
    object-fit: cover;
  }
#iframe1{
  width: 99% !important;
  height: 99% !important;
}
.infoVideo{
    padding-top:1.25%;
    position:relative;
}
.styleIframe {
    position:absolute;
    top:0; 
    left: 0;
    
}
.wrapper{
  background-color: transparent;
  position: absolute;
  z-index: 100;
  padding:10px 20px;
}
#card_videos {
  margin-top: 10px; /* Ajusta el valor si es necesario */
}

iframe {
  width: 100% !important;
  height: 100% !important;
  
}
#contenedorItemVideo {
  background-color: transparent;
  color: azure;
  overflow: hidden;
}
.cursorPointer{
  cursor: pointer;

}
.card{
  width: 300px;
  background-color: transparent !important;

}
.card-width{
  width: 300px;


}
.card-video{
  border:2px solid transparent !important;


  
}

.card-video:hover{
  background-color: #242334 !important;
  border:2px solid #0d3eb9 !important;
}
.card-temporada:hover{
  background-color: #ebeaf8 !important;
  border:2px solid #0d3eb9 !important;
}
.truncate-text {
  display: -webkit-box;
  -webkit-line-clamp: 2; /* número de líneas para mostrar */
  -webkit-box-orient: vertical;  
  overflow: hidden;
}

.truncate-text-6 {
  display: -webkit-box;
  -webkit-line-clamp: 6; /* número de líneas para mostrar */
  -webkit-box-orient: vertical;  
  overflow: hidden;
}


@media (max-width: 1000px) {
  .wrapper{
    position: relative;
    margin:0px !important;
    padding:10px;
  }

}
@media (max-width: 1180px) {
  .containerLinksNavbar{
    display: none;
    }
}

@media (max-width: 650px) {
  .infoVideo{
    padding-top:0.25%;
    position:relative;
    padding-bottom: 30px;
  }
  .iframe1 {
    height: 380px;
  }
  

}
.img-preview{
  border:2px solid #c4c4c4 !important;
  display: none;
}
.content-preview{


}
.img-serie{
  width:100%;
  filter: brightness(.5);
  position: relative;
}
.info-serie{
}
